import { isEmpty, map, filter, forEach } from "lodash";

import type {
  Evaluation,
  FilterEvaluation,
} from "@/typings/EvaluationInterface";

export const localDateOptions = {
  weekday: "long",
  year: "numeric",
  month: "long",
  day: "numeric",
};

export const unique = (
  entries: object[],
  object_key: string,
  column_key: string
): object[] => {
  if (!isEmpty(entries) && !isEmpty(entries[0])) {
    return [
      ...new Set(
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        map(entries, (entry) => entry[object_key]).map((obj) => obj[column_key])
      ),
    ];
  }
  return entries;
};

export const compareEntries = (
  key: string,
  column: string,
  direction = "asc"
) => {
  // return function innerSort<T>(a: T, b: T) {
  return function innerSort(a: any, b: any) {
    let varA;
    let varB;
    let comparison = 0;

    // check if property exist on objects
    // eslint-disable-next-line no-prototype-builtins
    if (!a[key].hasOwnProperty(column) || !b[key].hasOwnProperty(column))
      return 0;

    if (column === "due_date") {
      varA =
        typeof a[key][column] === "string"
          ? Date.parse(a[key][column])
          : a[key][column];
      varB =
        typeof b[key][column] === "string"
          ? Date.parse(b[key][column])
          : b[key][column];
    } else if (column === "is_result_released") {
      varA =
        typeof a[key][column] === "boolean"
          ? Boolean(a[key][column])
          : a[key][column];
      varB =
        typeof b[key][column] === "boolean"
          ? Boolean(b[key][column])
          : b[key][column];
    } else {
      varA =
        typeof a[key][column] === "string" ? a[key][column] : a[key][column];
      varB =
        typeof b[key][column] === "string" ? b[key][column] : b[key][column];
    }

    if (varA > varB || varA === true) {
      comparison = 1;
    } else if (varA < varB || varA === false) {
      comparison = -1;
    }
    return direction === "desc" ? comparison * -1 : comparison;
  };
};

export const filterEntries = (
  entries: Evaluation[],
  filters: FilterEvaluation
): object[] | boolean[] => {
  let newEntries: object[] | Evaluation[] | boolean[] | undefined = entries;

  if (filters?.timeframe !== "all") {
    const _entries: object[] | boolean[] = [];
    filter(newEntries, (entry: Evaluation | any) => {
      // eslint-disable-next-line no-prototype-builtins
      if (!entry.hasOwnProperty("course")) return 0;
      if (
        "course" in entry &&
        entry?.course?.term?.includes(<string>filters?.timeframe)
      ) {
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        _entries.push(entry);
      }
    });
    newEntries = _entries;
  }

  if (!isEmpty(filters.limit)) {
    const filtered_entries: Evaluation[] | object[] = [];

    filter(newEntries, (entry: Evaluation | any) => {
      // eslint-disable-next-line no-prototype-builtins
      if (!entry.hasOwnProperty("event")) return 0;
      // if (entry?.event?.is_submitted === "1") {
      if (entry?.is_submitted === "1") {
        forEach(filters.limit, (item) => {
          if (
            item === "can_edit" &&
            "event" in entry &&
            !entry?.event?.is_result_released
          ) {
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore
            filtered_entries.push(entry);
          }
          if (
            item === "can_view" &&
            "event" in entry &&
            entry?.event?.is_result_released
          ) {
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore
            filtered_entries.push(entry);
          }
        });
      }
    });
    /** return filtered_entries */
    newEntries = filtered_entries;
  }
  return newEntries;
};

export const paginateEntries = (
  entries: Evaluation[],
  offset: number,
  end: number
) => {
  if (!isEmpty(entries)) {
    return entries.slice(Number(offset), Number(end));
  }
  return entries;
};
