export interface ResponseError {
  type: string;
  status?: number;
  message: string;
  statusText?: string;
}

type State = {
  data: null;
  pending: boolean;
  status: string | null;
};

// httpClient: () => Promise<void>;
// HttpRefresh: () => Promise<void>;

interface Options {
  method?: string;
  headers?: object;
  body?: object | URLSearchParams;
}

const baseUrl = "/";
// method = "GET",
// body?: HTMLFormElement | URLSearchParams
export default function useFetch(
  endpoint: string | Request,
  options?: Options
) {
  const state = reactive<State>({
    pending: true,
    status: null,
    data: null,
  });

  const { signal, abort } = new AbortController(); // AbortSignal | null | undefined

  const headers = {
    Accept: "application/json",
    "Content-Type": "application/json;charset=UTF-8",
  };
  // Authorization: "Bearer ${IPEER session}",

  if (typeof endpoint === "string") {
    endpoint = endpoint.replace(/^\//, "");
  }

  if (options?.headers) {
    options.headers = { ...headers, ...options.headers };
  } else {
    options = {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json;charset=UTF-8",
        Authorization: "Bearer ${IPEER session}",
      },
    };
  }

  async function httpClient(message?: any) {
    try {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      const response = await fetch(`${baseUrl}${endpoint}`, {
        signal,
        ...options,
      });
      if (!response.ok) {
        state.status = await response.json();
        return;
      }
      const json = await response.json();
      if (json?.data) {
        state.data = json?.data;
      } else {
        state.status = json;
      }
    } catch (err: Error | any) {
      state.status = err;
    } finally {
      state.pending = false;
    }
  }

  async function httpRefresh() {
    try {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      const response = await fetch(`${baseUrl}${endpoint}`, options);
      if (!response.ok) {
        state.status = await response.json();
        // throw new Error((await response.json()).message);
        return;
      }

      state.data = (await response.json()).data;
      // const json = await response.json();
      // if (json?.data) {
      //   state.data = json.data;
      // } else {
      //   state.data = json;
      // }
    } catch (err: Error | any) {
      state.status = err;
    } finally {
      state.pending = false;
    }
  }

  return {
    ...toRefs(readonly(state)),
    httpRefresh,
    httpClient,
    abort,
  };
}
