import { format, isTomorrow, isValid, parseISO } from "date-fns";
import { enCA } from "date-fns/locale";

/**
 * Starting with v2.0.0-beta.1 date-fns doesn't accept strings as date arguments.
 * Strings should be parsed using parseISO (ISO 8601) or parse
 * Please use `parseISO` to parse strings.
 * See: https://github.com/date-fns/date-fns/blob/master/docs/upgradeGuide.md#string-arguments
 * @param date
 */
export function is_overdue(date: string | undefined) {
  return isTomorrow(parseISO(<string>date));
}

export function is_due_tomorrow(date: string | undefined) {
  return isTomorrow(parseISO(<string>date));
}

export function short_date_format(date: string | undefined) {
  if (date != null) {
    const formatted_date = parseISO(<string>date);
    return format(formatted_date, "MMM d, yyyy", { locale: enCA });
  }
}

export function long_date_format(date: string | undefined) {
  if (date != null) {
    const formatted_date = parseISO(<string>date);
    return formatted_date
      ? isValid(formatted_date)
        ? format(formatted_date, "EE, MMM d, yyyy @ h:mm aaaa", {
            locale: enCA,
          })
        : "Invalid date"
      : " ";
  }
}
