export function shortDateFormat(date: string | undefined) {
  if (date != null) {
    const formatted_date = parseISO(date);
    return format(formatted_date, "MMM d, yyyy", { locale: enCA });
  }
}

export function longDateFormat(date: string | undefined) {
  if (date != null) {
    const formatted_date = parseISO(date);

    return formatted_date
      ? isValid(formatted_date)
        ? format(formatted_date, "EE, MMM d, yyyy @ h:mm aaaa", {
            locale: enCA,
          })
        : "Invalid date"
      : " ";
  }
}

export function submittedDateFormat(date: string | undefined) {
  if (date != null) {
    const formatted_date = parseISO(date);
    const d = formatted_date
      ? isValid(formatted_date)
        ? format(formatted_date, "EE, MMM d", {
            locale: enCA,
          })
        : "Invalid date"
      : " ";
    const t = formatted_date
      ? isValid(formatted_date)
        ? format(formatted_date, "h:mm aaaa", {
            locale: enCA,
          })
        : "Invalid date"
      : " ";
    return `${d} at ${t}`;
  }
}

interface FormatDate {
  date: string;
  format: string;
  icon: boolean;
}

export function FormatDate(props: FormatDate) {
  return h(
    "div",
    {
      class: "due-date",
      style: { display: "flex", "align-items": "center", gap: "0.25rem" },
    },
    [
      props.icon
        ? h(
            "span",
            {
              class: "svg-clock",
              innerHTML: CLOCK,
              style: { "margin-bottom": "-0.15rem" },
            },
            []
          )
        : null,
      h(
        "span",
        {
          style: { "font-size": "95%" },
          class: "date",
          innerHTML:
            props.format === "short"
              ? shortDateFormat(props.date)
              : props.format === "long"
              ? longDateFormat(props.date)
              : props.format === "submitted"
              ? submittedDateFormat(props.date)
              : parseISO(props.date),
        },
        []
      ),
    ]
  );
}

FormatDate.props = {
  date: String,
  format: String,
  icon: Boolean,
};

interface ReleaseMessageDate {
  date: string;
  released: boolean;
  submitted?: string | boolean;
  resultReleased: boolean;
  releaseDateBegin: string;
}

// row?.is_submitted && row?.event?.is_released && !row?.event?.is_result_released
export function ReleaseMessageDate(props: ReleaseMessageDate) {
  return h(
    "span",
    { class: "text-sm" },
    props.released &&
      props.submitted &&
      !props.resultReleased &&
      !compareAsc(new Date(), parseISO(props.releaseDateBegin))
      ? [
          `Peers' reviews of you will be available starting ${shortDateFormat(
            props.date
          )}`,
        ]
      : ""
  );
}

ReleaseMessageDate.props = {
  date: String,
  released: Boolean,
  submitted: String || Boolean,
  resultReleased: Boolean,
  releaseDateBegin: String,
};

interface YourStatus {
  released?: boolean;
  resultReleased?: boolean;
  submitted?: string | boolean;
}

export function YourStatus(props: YourStatus) {
  return h(
    "div",
    {
      class: "your-status",
      style: { display: "flex", "align-items": "center", gap: "0.25rem" },
    },
    parseInt(String(props.submitted))
      ? [
          h("span", {
            innerHTML: CHECKMARK,
            style: { "margin-bottom": "-0.25rem" },
          }),
          h(
            "span",
            {
              class: "completed",
              style: {
                color: "#4682b4",
                "font-weight": "400",
                "margin-bottom": "-0.25rem",
              },
            },
            [`Completed`]
          ),
        ]
      : [
          h("span", {
            innerHTML: TIMES,
            style: { "margin-bottom": "-0.25rem" },
          }),
          h(
            "span",
            {
              class: "not-done",
              style: { color: "#666", "font-weight": "400" },
            },
            [`Not Done`]
          ),
        ]
  );
}

YourStatus.props = {
  released: Boolean,
  resultReleased: Boolean,
  submitted: String || Boolean,
};

