interface IsDueTomorrow {
  date?: string | null;
}

/**
 * IsDueTomorrow
 * @param props
 * @constructor
 * return 'Due Tomorrow' text if true
 */
export function IsDueTomorrow(props: IsDueTomorrow) {
  if (props.date != null) {
    const tomorrow = isTomorrow(parseISO(props.date));
    if (tomorrow) {
      return h(
        "span",
        {
          class: "text-sm",
          style: {
            "background-color": "#daa520",
            color: "white",
            padding: "0 0.5rem 0 0.5rem",
            "font-weight": "500",
          },
        },
        ["Due Tomorrow"]
      );
    }
  }
}

IsDueTomorrow.props = {
  date: String,
};
